@import url(https://fonts.googleapis.com/css?family=Cormorant|Quattrocento+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --trans: all 300ms ease-in-out;
  --title: 'Cormorant', serif;
  --text: 'Quattrocento Sans', sans-serif;
}

body, html {
  overflow: hidden;
  box-sizing: border-box;
  height: 100vh;
  background-color: #333;
  color: #fff;
}

h1 {
  margin: 0;
}
#sidenav-1 {
  background-color: #222;
}

.sidenav .user-view {
  margin-bottom: 0;
}

.sidenav li>a {
  margin: 0;
  padding: 5px 32px;
  font-size: 18px;
  line-height: 38px;
}

.sidenav li p{
  line-height: 25px;
}

.sidenav .divider {
  margin: 0;
}

.score-bored {
  height: 300px;
  padding: 0 30px;
}

hr {
  width: 80%;
}

.box {
  border: 2px dashed #666;
  padding: 0 15px;
  margin-top: 100px;
}
.contain {
	margin-top: 10px;
	padding: 10px 20px 10px 320px;
	height: 100vh;
	width: 100%;
}

@media only screen and (max-width : 992px) {
	.contain {
		padding: 0 10px;
  }
}
.card {
    box-shadow: none;
    height: 190px;
    width: 190px;
    background-color: #333;
    margin: 20px auto;
    border: 1px solid #333;
}

.card .card-title {
    font-weight: bold;
    text-shadow: 0px 0px 10px rgb(241, 241, 241);
}

.card:hover .card-image{
    margin-top: -10px;
}

.card:hover .card-image::after { 
    content:'';
    display: block;
    position: absolute;
    left: 20%;
    bottom: 30px;
    width: 60%;
    height: 8px;
    background: transparent;
    border-radius: 100px/50px;
    box-shadow: 0 50px 40px rgba(255, 255, 255, 0.61);
}

@media (max-width: 425px) {
    .card {
        margin: 5px auto;
        height: 80px;
        width: 80px;
    }
}
.topnavcls {
    background-color: #222;
    height: 70px;
}

.small-title {
    margin: 5px auto;
}

.score-bored-small {
    margin-top: -7px;
}
